
@media screen and (min-width: 1200px) {
  html {
    font-size: 85%;
  }
}
@media screen and (max-height: 800px) and (min-width: 1200px) {
  html{
    font-size: 70%;
  }
}

@media screen and (min-width: 1920px) {
  html {
    font-size: 95%;
  }
}
@media screen and (min-width: 2880px) {
  html {
    font-size: 150%;
  }
}
@media screen and (min-width: 3960px) {
  html {
    font-size: 200%;
  }
}
