.title {
  font-weight: 900;

  @include xs-block {
    font-size: 48px;
    text-align: center;
    margin-bottom: rem(32);
    letter-spacing: -1.392px;
    margin-top: 120px;
  }

  @include lg-block {
    text-align: left;
    font-size: 62px;
  }
  @include sm-block {
    word-wrap: wrap;
    font-size: 58px;
    margin-bottom: rem(60);
  }
  @include xl-block {
    font-size: rem(82);
    font-weight: 900;
    letter-spacing: -2.378px;
  }
}

.text {
  color: #9297a2;

  @include xs-block {
    text-align: center;
    font-size: rem(22);
    margin-bottom: rem(30);
  }
  @include sm-block {
    font-size: rem(28);
  }
  @include lg-block {
    font-size: rem(24);
    text-align: left;
  }
  @include xl-block {
    color: #9297a2;
    font-size: rem(28);
    margin-bottom: rem(60);
  }
}

.section_link {
  font-weight: bold;
  font-size: rem(28);

  a {
    background: no-repeat url('../img/svg/Arr_for_link.svg');
    background-position: rem(10) rem(0);
    background-size: rem(20);
    padding-left: rem(40);
  }

  @include xs-block {
    width: 90%;
    margin-top: 13%;
    text-align: center;
  }
  @include sm-block {
    width: 100%;

    text-align: center;
  }
  @include md-block {
    width: 95%;

    text-align: center;
  }
  @include lg-block {
    position: absolute;
    display: flex;
    bottom: 0px;
    margin-top: 0;
    margin-bottom: 0;
    width: 100%;
  }
  @include xl-block {
    position: absolute;
    display: flex;
    margin-top: 0;
    width: 100%;
  }
}

.downloads_icons {
  @include xs-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    height: 147px;
    align-self: flex-start;
    margin-bottom: 20px;
