    margin: 0 auto;
    width: 600px;
    justify-content: space-evenly;
    margin-bottom: 0;
    height: 60px;
    img {
      width: auto;
      height: 70;
    }
  }

  @include lg-block {
    width: rem(362);
    height: rem(48);
    justify-content: space-between;
    margin: 0;
    align-self: flex-start;
    img {
      width: auto;
      height: rem(48);
    }
  }

  @include xl-block {
    width: rem(442);
    justify-content: space-between;
    margin: 0;
    img {
      width: auto;
      height: rem(58);
    }
  }
}

#about {
  @include lg-block {
    height: 100vh;
  }
}

.about {
  @include xs-block {
    margin-bottom: 120px;
  }
  @include lg-block {
    height: 100vh;
    position: relative;
    margin-bottom: 0px;
  }
  .content {
    @include xs-block() {
      height: max-content;
    }
    @include lg-block {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: space-between;
    }
    @include xl-block {
      margin-top: rem($mr_from_header);
    }

    .description {
      position: relative;

      @include size-xs(13);
      @include size-lg(6);
      @include xs-block {
        display: flex;
        flex-flow: nowrap column;
        justify-content: flex-start;
        align-items: center;
        height: 100%;
      }
      @include lg-block {
        align-items: flex-start;
      }
      @include xl-block {
        position: static;
        align-items: flex-start;
      }
      .title {
        @include xs-block {
          margin-top: 0;
        }
      }

      .text {
        @include xs-block {
          margin-bottom: rem(40);
        }
      }
    }

    .viwe {
      height: 110vh;
      background: #0c1723;
      z-index: -1;
      border-bottom: none;
      border: rem(25) solid #eeeeee;

      @include xs(display, none);
      @include size-md(12);
      @include size-lg(5);
      @include size-xl(6);

      @include lg-block {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 400px;
        position: absolute;
        top: 3vh;
        left: 49.6%;
        border-radius: 68px;
      }
      @include xl-block {
        position: absolute;
        top: 3vh;
        left: 52.7%;
        border-radius: rem(88);
      }

      .top_of_viwe {
        border-bottom: rem(2) solid #242e39;

        @include lg-block {
          margin: 0 20px;
          padding: 25px 0 30px 0;
        }

        @include xl-block {
          margin: 0 rem(40);
          padding: rem(37) 0 rem(39) 0;
        }

        .head {
          position: relative;
          display: flex;
          justify-content: space-evenly;
          align-items: center;
          color: white;
          box-shadow: 0px rem(-10) rem(28) rgba(0, 0, 0, 0.06);
          background: no-repeat url('../img/svg/steve.svg'),
            rgba(255, 255, 255, 0.05);
          background-size: 15%;
          @include lg-block {
            width: 300px;
            border-radius: 22px;
            height: 90px;
            background-position: 10px center;
          }

          @include xl-block {
            width: rem(421);
            height: rem(100);
            border-radius: rem(28);
            background-position: rem(22) center;
          }

          &:after {
            content: '';
            position: absolute;
            bottom: rem(25);
            left: rem(80);
            background: no-repeat url('../img/svg/stars.svg');
            background-size: contain;
            width: rem(107);
            height: rem(15);

            @include lg-block {
              left: 77px;
            }

            @include xl-block {
              left: rem(96);
            }
          }

          .steve_name {
            line-height: rem(30);
            height: rem(53);
            border-right: rem(2) solid rgba(255, 255, 255, 0.1);

            @include lg-block {
              padding-left: 65px;
              padding-right: 10px;
              font-size: 16px;
            }

            @include xl-block {
              font-size: rem(20);
              padding-left: rem(70);
              padding-right: rem(30);
            }
          }

          .per {
            text-transform: uppercase;
            margin-bottom: 0px;

            @include lg-block {
              font-size: 16px;
            }

            @include xl-block {
              font-size: rem(20);
            }

            p {
              color: #9297a2;
              text-transform: none;
              margin: 0;
              margin-top: rem(10);

              @include lg-block {
                font-size: 12px;
                margin-left: 3px;
              }
              @include xl-block {
                margin-left: rem(5);
                font-size: rem(14);
              }
            }
          }
        }
      }

      .chat {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        color: white;
        font-weight: 300;

        @include lg-block {
          transform: translateY(352px);
          width: calc(100% - 40px);
          font-size: 14px;
          animation: move_chat_lg 20s 3s linear forwards;
        }
        @include xl-block {
          transform: translateY(rem(402));
          width: calc(100% - 3.6875rem);
          font-size: rem(16);
          animation: move_chat 20s 3s linear forwards;
        }

        li {
          @include chat_message();
          animation: show_message 0.5s linear forwards;
          opacity: 0;

          &:nth-child(1) {
            animation-delay: 1.5s;
          }
          &:nth-child(2) {
            animation-delay: 4s;
          }
          &:nth-child(3) {
            animation-delay: 7s;
          }
          &:nth-child(4) {
            animation-delay: 10s;
          }
          &:nth-child(5) {
            animation-delay: 13s;
          }
          &:nth-child(6) {
            animation-delay: 16s;
          }
        }
      }
      .user {
        align-self: flex-end;
        background: #0072ff;
        display: inline-block;
      }

      .steve {
        background: rgba(255, 255, 255, 0.05);
        align-self: flex-start;
      }

      .steve_typing {
        @include typing();
        animation: show_typing_steve 20s linear forwards;

        @include lg-block {
          left: rem(20);
          top: rem(600);
        }

        @include xl-block {
          left: rem(40);
          top: rem(650);
        }
      }

      .user_typing {
        @include typing();
        animation: show_typing_user 20s linear forwards;

        @include lg-block {
          right: rem(20);
          top: rem(600);
        }

        @include xl-block {
          right: rem(40);
          top: rem(650);
        }
      }
    }

    .section_link {
      margin-bottom: 120px;
      @include sm-block {
        margin-bottom: 18%;
      }
      @include lg-block {
        position: absolute;
        display: flex;
        bottom: rem(70);
        margin-bottom: 0;
        width: 100%;
      }
      @include xl-block {
        bottom: rem(90);
      }
    }
  }
}

#how_it_works {
  background: #0c1723;

  @include xs-block {
    height: 1196px;
  }
  @include sm-block {
    height: 1160px;
  }
  @include md-block {
    height: 1110px;
  }
  @include lg-block {
    height: 101vh;
  }
}

.how_it_works {
  color: #fff;
  position: relative;
  height: 101vh;

  .header {
    @include xs-block {
      display: none;
    }
    @include lg-block {
      display: flex;
    }
  }

  .content {
    display: flex;
    position: relative;

    @include xs-block {
      flex-direction: column;
      flex-wrap: wrap;
    }
    @include lg-block {
      flex-direction: row;
      justify-content: space-between;
    }
    @include xl-block {
      margin-top: rem($mr_from_header);
    }

    .description {
      @include description;
      @include size-xs(12);
      @include size-lg(6);

      @include xs-block {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: min-content;
        position: static;
      }
      @include xl-block {
        margin-top: 0px;
      }

      .how_it_works__title {
        margin-bottom: 0;

        @include sm-block {
          margin-top: 80px;
        }
        @include lg-block {
          margin-top: 0;
        }
      }

      .how_it_works__text {
        margin-bottom: 0;
        margin-top: rem(60);
      }
    }

    .viwe {
      box-sizing: border-box;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      align-items: center;

      @include xs-block {
        position: relative;
        width: 100%;
        overflow: visible;
        left: 0;
        border: none;
        padding-top: 30px;
      }
      @include size-lg(5);
      @include lg-block {
        position: absolute;
        align-items: center;
        height: 96vh;
        border-radius: 0px 0px 68px 68px;
        border: rem(25) solid #19232f;
        width: 400px;
        border-top: none;
        overflow: hidden;
        left: 49.6%;
        top: rem(-200);
      }
      @include size-xl(6);
      @include xl-block {
        padding: 0;
        border-radius: 0px 0px rem(80) rem(80);
        left: 52.7%;
        top: rem(-325);
      }

      .viwe__title {
        font-weight: bold;
        border-bottom: solid rem(1) #242e39;

        @include xs-block {
          box-sizing: border-box;
          text-align: center;
          padding: 39px 70px;
          font-size: 28px;
          width: 315px;
        }
        @include sm-block {
          @include size-sm(12);
          font-size: 44px;
        }
        @include lg-block {
          text-align: center;
          padding: 20px 0 39px 0;
          font-size: rem(28);
          width: 300px;
          margin: 22px 35px 0;
        }
        @include xl-block {
          text-align: center;
          padding: rem(39) 0;
          width: rem(420);
          margin: rem(85) rem(35) 0;
        }
      }

      .featches {
        padding-top: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: rem(60);

        @include xs-block {
          width: 315px;
        }

        @include size-sm(12);
        @include lg-block {
          width: rem(300);
        }
        @include size-xl(12);

        .feache__item {
          box-sizing: border-box;
          color: #000;
          margin-bottom: 4.9vh;
          position: relative;
          border-radius: rem(28);

          @include xs-block {
            width: 100%;
            padding: 25px 0 25px 100px;
            margin-bottom: 55px;
          }
          @include lg-block {
            height: rem(100);
            padding: 15px 0 25px 100px;
            margin-bottom: rem(40);
            opacity: 0;
          }
          @include size-xl(12);
          @include xl-block {
            padding: rem(25) 0 rem(25) rem(100);
            opacity: 0;
          }

          &:nth-child(1) {
            background: no-repeat url('../img/svg/ic.svg'), #ffffff;
            @include feache_bg();
            &::before {
              content: '1';
              @include feach_icon;
            }
          }
          &:nth-child(2) {
            background: no-repeat url('../img/svg/ic2.svg'), #ffffff;
            @include feache_bg();

            &::before {
              content: '2';
              @include feach_icon;
            }
          }
          &:nth-child(3) {
            background: no-repeat url('../img/svg/ic3.svg'), #ffffff;
            @include feache_bg();

            &::before {
              content: '3';
              @include feach_icon;
            }
          }

          h4 {
            font-size: rem(20);
          }

          p {
            margin: rem(10) 0 0 0;
            font-size: rem(14);
            color: #9297a2;
          }
        }
      }
    }
  }

  .section_link {
    @include xs-block {
      width: 170px;
      position: absolute;
      left: calc(50% - 110px);
      padding-left: 0;
      background-position: 30px 5px;
      bottom: -192px;
    }
    @include sm-block {
      width: 220px;
      margin-top: 0;
      bottom: -150px;
      left: calc(50% - 110px);
    }
    @include md-block {
      bottom: -90px;
      left: calc(50% - 120px);
    }
    @include lg-block {
      width: rem(170);
      left: 0;
      bottom: rem(70);
    }
    @include xl-block {
      bottom: rem(90);
    }

    .how_it_works__selection_link {
      color: #fff;
      background: no-repeat url('../img/svg/Arr_for_link_white.svg');
      background-position: rem(10) rem(0);
      background-size: rem(20);
      padding-left: rem(40);

      @include xs-block {
        display: inline-block;
        width: 100%;
        background-position: 13px;
      }
    }
  }
}

#download {
  position: relative;
  @include lg-block {
    height: 100vh;
    background: no-repeat url('../img/svg/main_bg.svg');
    background-position: 4vw -29vh;
    background-size: 1400px;
  }
  @include xl-block {
    background-size: rem(1700);
  }
  @media screen and (min-width: 1920px) {
    background-size: rem(1570);
  }

  $space_x: 200;
  $space_y: 320;
  $width: 1200;

  @for $i from 0 through 8 {
    @media screen and (min-width: rem($width + ($i * 100))) {
      background-position-x: rem($space_x + ($i * 50));
      background-position-y: rem(-$space_y);
    }
  }
  
  @media screen and (max-height: 800px) and (min-width: 1200px) {
    background-size: rem(1470);
    $space_x_h: 430;

    @for $i from 0 through 8 {
      @media screen and (min-width: rem($width + ($i * 100))) {
        background-position-x: rem($space_x_h + ($i * 70));
        background-position-y: rem(-$space_y);
      }
    }
  }

  @media screen and (max-height: 800px) and (min-width: 1920px) {
    background-size: rem(1470);
    $space_x_2: 50;

    @for $i from 0 through 8 {
      @media screen and (min-width: rem($width + ($i * 100))) {
        background-position-x: rem($space_x_2 + ($i * 75));
        background-position-y: rem(-$space_y);
      }
    }
  }

  @media screen and (min-width: 3960px) {
    background-size: rem(1570);
    $space_x_3: -300;

    @for $i from 0 through 8 {
      @media screen and (min-width: rem($width + ($i * 100))) {
        background-position-x: rem($space_x_3 + ($i * 110));
        background-position-y: rem(-$space_y);
      }
    }
  }
}

